.hero {
	width: 100%;
	height: 100vh;
	overflow: auto;

	.bg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: url('../img/hero.png');
		background-size: cover;
		background-position: center;
	}

	.welcome {
		margin-top: 16px;
		z-index: 1;
		display: inline-block;
		text-transform: uppercase;
		font-size: 18px;
		font-weight: normal;
		letter-spacing: 4px;

		@media only screen and (min-width: 768px) {
			margin-top: 32px;
			font-size: 28px;
		}
	}
}